import React from 'react'

import { WithRoot } from '../atoms/WithRoot'
import { GCompanyBranch } from '../graph/types'

interface IUserCompanyBranchesProps {
  render(branches: GCompanyBranch.Fragment[], isLoading: boolean): ReactNode
}

export const UserCompanyBranches: React.SFC<IUserCompanyBranchesProps> = ({
  render,
}) => (
  <WithRoot
    render={({ user }) =>
      user.branches ? render(user.branches, false) : render([], true)
    }
  />
)
