import React from 'react'
import { IFormApi, IFormState } from 'react-form'

import { isFormValid } from '../helpers/form'
import { ITouchButtonProps, TouchButton } from '../molecules/TouchButton'
import { Colors } from '../styling'

interface IProps extends ITouchButtonProps {
  form: IFormState & IFormApi
}

export const FormSubmit: React.SFC<IProps> = ({ children, form, ...props }) => (
  <TouchButton
    color={Colors.green}
    disabled={!isFormValid(form) || form.submitting}
    onClick={e => {
      e.preventDefault()
      form.submitForm(e)
    }}
    {...props}
  >
    {children}
  </TouchButton>
)
