import React from 'react'
import { Props } from 'react-select/lib/Select'

import { CompanyBranch } from '../atoms/CompanyBranch'
import { SelectControl } from '../atoms/SelectControl'
import { GCompanyBranch } from '../graph/types'
import { RM } from '../helpers/ramda'
import { i18n } from '../i18n'
import { ICompanyBranchPickerImpl } from '../organisms/CompanyBranchPicker'

interface IProps
  extends ICompanyBranchPickerImpl,
    Props<GCompanyBranch.Fragment> {}

export const CompanyBranchDropdown: React.SFC<IProps> = ({
  branches,
  selectedBranch,
  onSingleSelection,
  selectedBranches,
  onMultiSelection,
  ...props
}) => (
  <SelectControl<GCompanyBranch.Fragment>
    value={props.isMulti ? selectedBranches : selectedBranch}
    placeholder={i18n.t`Filter branches...`}
    options={branches}
    getOptionValue={RM.prop('id')}
    formatOptionLabel={branch => <CompanyBranch branch={branch} />}
    filterOption={(branch, rawInput) =>
      branch.data.name
        .toLocaleLowerCase()
        .includes(rawInput.toLocaleLowerCase())
    }
    onChange={selection => {
      if (props.isMulti && onMultiSelection && Array.isArray(selection)) {
        onMultiSelection(selection)
      }
      if (!props.isMulti && onSingleSelection) {
        if (Array.isArray(selection)) {
          onSingleSelection(RM.head(selection) || null)
        } else {
          onSingleSelection(selection || null)
        }
      }
    }}
    {...props}
  />
)
