import React from 'react'
import { Route, RouteComponentProps, Switch } from 'react-router'

import { PageTitle } from '../../../atoms/PageTitle'
import { i18n } from '../../../i18n'
import { NotFoundPage } from '../../../pages/NotFoundPage'
import { LazyRoute } from '../../../routes/LazyRoute'
import { SecureRoute } from '../../../routes/SecureRoute'
import { BranchPreferencesRoutes } from '../../branchpreferences/routes/BranchPreferencesRoutes'
import { BusinessHoursRoutes } from '../../businesshours/routes/BusinessHoursRoutes'
import { SettingsPage } from '../pages/SettingsPage'

interface ISettingsRoutesProps extends RouteComponentProps<{}> {}

export const SettingsRoutes: React.SFC<ISettingsRoutesProps> = ({ match }) => (
  <>
    <PageTitle.Route path={match.path} title={i18n.t`Settings`} />
    <Switch>
      <SecureRoute exact={true} path={match.path} component={SettingsPage} />
      <LazyRoute
        path={`${match.path}/menu`}
        component={React.lazy(() =>
          import('../../../segments/menu/routes/MenuRoutes'),
        )}
      />
      <LazyRoute
        path={`${match.path}/zones`}
        component={React.lazy(() =>
          import('../../../segments/zones/routes/ZoneMenuRoutes'),
        )}
      />
      <Route
        path={`${match.path}/businesshours`}
        component={BusinessHoursRoutes}
      />
      <Route
        path={`${match.path}/preferences`}
        component={BranchPreferencesRoutes}
      />
      <Route component={NotFoundPage} />
    </Switch>
  </>
)

export default SettingsRoutes
